import { Box, Button, IconButton, Typography, useTheme } from "@mui/material";
import { tokens } from "../../theme";
import Header from '../../components/Header';
import React, { useState, useEffect } from 'react';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';

const Homepage = () => {
    // const theme = useTheme();
    // const colors = tokens(theme.palette.mode);
    const [wildfireList, setWildfireList] = useState({
        fires: []
    });

    const columns = [
        { filed: 'county', headerName: 'County', flex: 1 },
        { filed: 'name', headerName: 'Name', cellClassName: 'name-column--cell' },
        { filed: 'acresBurned', headerName: 'Acres Burned', flex: 1 },
        { filed: 'percentContained', headerName: 'Percent Contained', flex: 1 },
        { filed: 'isActive', headerName: 'Is Active', flex: 1 }
    ]

    useEffect(() => {
        const corsProxyUrl = 'https://thingproxy.freeboard.io/fetch/';
        fetch(corsProxyUrl + 'https://www.fire.ca.gov/umbraco/api/IncidentApi/List?inactive=true&amp;year=2022')
            .then((res) => res.json())
            .then((res) => {
                console.log(res);
                setWildfireList({
                    fires: [...res]
                });
                console.log(wildfireList);
            })
            .catch((error) => {
                console.log('error....' + error);
            });
    }, []);

    return(
        <Box m="20px">
            <Header title="Wildfires" subtitle="View All The Wildfires Based on County" />
            <Box
                m="40px 0 0 0"
                height="75vh"
                sx={{
                '& .MuiDataGrid-root': {
                    border: 'none'
                },
                '& .MuiDataGrid-cell': {
                    borderBottom: 'none'
                },
                '& .pol-column--cell': {
                    // color: colors.greenAccent[300],
                    fontWeight: 'bold'
                },
                '& .MuiDataGrid-columnHeaders': {
                    // backgroundColor: colors.blueAccent[700],
                    borderBottom: 'none'
                },
                '& .MuiDataGrid-virtualScroller': {
                    // backgroundColor: colors.primary[400]
                },
                '& .MuiDataGrid-footerContainer': {
                    borderTop: 'none',
                    // backgroundColor: colors.blueAccent[700]
                },
                '& .MuiDataGrid-toolbarContainer .MuiButton-text': {
                    // color: `${colors.grey[100]} !important`
                }
                }}
            >
                <DataGrid
                rows={wildfireList.fires}
                columns={columns}
                components={{
                    Toolbar: GridToolbar
                }}
                />
            </Box>
        </Box>
    );
};

export default Homepage;